        <v-col v-for="(item, index) in items" :key="index + 'tpitmes'" cols="12" sm="6" md="4" lg="3">
          <div class="team-item"><img v-if="item.avatar" alt="pic" width="100%" class="team-img mx-auto"
              :src="$store.state.host + item.avatar">
            <img v-else alt="pic" width="100%" class="team-img mx-auto" src="../assets/image/empty-photo.png">
            <div class="team-item-content">
              <v-tooltip color="black" v-if="$store.state.userId" top>
                <template v-slot:activator="{ on, attrs }">
                  <div v-on="on" v-bind="attrs" style="cursor: pointer" @click="toSeeProfile(item.id)">
                    <h3 style="color: #fff" v-if="item && item.first_name && item.last_name">{{ item.first_name }}
