<script>
import OtherPageSlide from "@/components/Misc/OtherPageSlide";
import axios from "axios";
import ProfileDialog from "@/components/Misc/ProfileDialog";
export default {
  name: "TeamPage",
  components: { ProfileDialog, OtherPageSlide },
  created() {
    this.fetchTeam();
  },
  data() {
    return {
      profileData: {
        idSelected: null,
        dialog: false,
      },
      items: []
    }
  },
  methods: {
    toSeeProfile(id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    fetchTeam() {
      axios.get(this.$store.state.host + '/accounts/api/v1/designers/').then(response => {
        this.items = response.data.data;
      })
    }
  },
}
</script>

